
import React from 'react'
import FilmeNoi from './FilmeNoi'
import {Switch, Route } from 'react-router-dom'
import { Film } from './Film'
import { SerialePopulare } from './SerialePopulare'
import { Serial } from './Serial'
import { Librarie } from './Librarie'

export const Main = () => {
  return (
    <div className="main">
      <div className="upperNav"></div>
      <div className="mainContent">
        <Switch>
          <Route path="/moviedb" exact component={FilmeNoi}></Route>
          <Route path="/moviedb/serialepopulare" exact component={SerialePopulare}></Route>
          <Route path="/moviedb/favorite" exact component={Librarie}></Route>
          <Route path="/moviedb/film/:id" component={Film}></Route>
          <Route path="/moviedb/serial/:id" component={Serial}></Route>
        </Switch>
      </div>
    </div>
  )
}

